[id="about"] {
  margin: var(--margin-sm);
}

@media (min-width: 768px) {
  [id="about"] {
    margin: var(--margin-md);

    header ~ section {
      margin: inherit auto;
      padding: 0 100px;
      width: calc(100% - 200px);
    }
  }
}

@media (min-width: 1200px) {
  [id="about"] {
    margin: var(--margin-lg);
  }
}
